import React from "react";
import { render } from "react-dom";
import { Chart } from "react-google-charts";
import spinner from '../../../static/images/spinner.gif';
import { ChartContent } from './styled';

const chartEvents = [
  {
    eventName: "select",
    callback({ chartWrapper }) {
      console.log("Selected ", chartWrapper.getChart().getSelection());
    }
  }
];

const data = [
  ["", ""],
  ["Mar", 170],
  ["", 220],
  ["May", 180],
  ["", 200],
  ["Jul", 100],
  ["", 230],
  ["Sep", 170],
  ["", 190],
  ["Nov", 230],
  ["", 220],
  ["Jan", 250]
];
 
const options = {
  title: "Months vs. Earnings",
  hAxis: { 
    title: "Earnings", 
    viewWindow: { min: 0, max: 4 }, 
    format: 'short', 
    gridlines: {count: 6} ,
    ticks: [130,150,200,250]
  },
  vAxis: { 
    title: "Months", 
    viewWindow: { min: 0, max: 4 } 
  },
  intervals: { style: 'sticks' },
  legend: "none"
};

class earningChart extends React.Component {
  render() {
    return (
      <ChartContent>
    		<Chart
    	      chartType="Line"
    	      data={data}
    	      options={options}
    	      graphID="Line"
    	      width="100%"
    	      height="480px"
    	      chartEvents={chartEvents}
            loader={<div className="spinner"><img src={spinner} /></div>}
        	/>
      </ChartContent>
    );
  }
}

export default(earningChart);
